@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600,800);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.styled-select { 
  width: -webkit-fit-content; 
  width: -moz-fit-content; 
  width: fit-content; 
  height: 34px; 
  border-radius: 10px;
  background-color: white;
  overflow: hidden;
  margin-right: 10px;
  padding-left: 8px;
  padding-right: 8px;
}
.App {
  text-align: center;
  background-color: #EAEFF3;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navContent {
    height: 90px;
    background-color: #112F70;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    text-align: center;
    margin-bottom: 50px;
}

@media only screen and (max-width: 450px) {
    .navContent {
        display: none;
    }
}

.navContent img {
    margin: 0 4rem;
    width: 40px;
    position: relative;
    top: 25%;
}

@media only screen and (max-width: 1008px) {
    .navContent img {
        margin: 0 3rem;
        width: 2rem;
    }
}

@media only screen and (max-width: 768px) {
    .navContent img {
        margin: 0 4.5%;
    }
}

@media only screen and (max-width: 417px) {
    .navContent img {
        margin: 0 4%;
    }
}

@media only screen and (max-width: 369px) {
    .navContent img {
        width: 8%;
        margin: 0 4%;
    }

    .navContent {
        height: 70px;
    }
}

@media only screen and (min-width: 450px) {
    .navBarResponsive {
        display: none !important;
    }
}


.navBarResponsive {
    display: flex;
    flex-direction: column;
    background-color: #112F70;
    text-align: left;
}
.navBarResponsive button {
    background: none;
    border: none;
    text-align: left;
}

.navBarResponsive button img {
    vertical-align: middle;
    padding: 0.3rem;
}

.navBarResponsive .open {
    display: flex;
    flex-direction: column;
    padding: 0 0.5rem 0.5rem;
    overflow-y: scroll;
}
.navBarResponsive .open a {
    text-decoration: none;
    color: white;
    margin-top: 0.8rem;
}
.navBarResponsive .open a img {
    width: 1rem;
    margin-right: 1rem;
}
.navBarResponsive .close {
    display: none;
}
.main-notif {
  padding-bottom: 50px;
}

.notif-box {
  background-color: #FFFFFF;
  width: 50%;
  height: 100%;
  margin: 0 auto;
  position: relative;
  text-align: left;
  padding-bottom: 40px;
  border-radius: 20px;
  box-shadow: 2px 2px 2px 2px rgb(209, 209, 209);
}

.notif-box::-webkit-scrollbar {
  display: none;
}

.container-notif {
  padding-top: 10px;
}

.container-notif h1 {
  color: #112F70;
  margin-left: 40px;
}

.container-notif button {
  background-color: rgb(224, 147, 3);
  color: #FFFFFF;
  border: none;
  font-size: 20px;
  border-radius: 10px;
  padding: 7px 15px;
  position: relative;
  margin-left: 75%;
}

.notif {
  background-color: #EAEFF3;
  font-size: 20px;
  color: #112F70;
  width: 80%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-radius: 10px;
  box-shadow: 1px 2px 2px rgb(209, 209, 209);
  position: relative;
}

.notif p {
  padding: 10px;
  margin-left: 20px;
}

.notif input {
  position: absolute;
  right: 10px;
  position: absolute;
  right: 10px;
  top: 35%;
}

@media only screen and (max-width: 586px) {
  .notif-box {
    width: 90%;
    padding-bottom: 0;
  }
  
  .container-notif form {
    height: 100% !important;
  }

  .container-notif h1{
    text-align: center;
    margin: 0;
    margin-top: 30px;
    font-size: 30px;
  }

  .container-notif button {
    margin: 0;
    margin-top: 35px;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 15px !important;
  }

  .notif p {
    font-size: 17px;
  }
}

@media only screen and (max-width: 1250px) {
  .notif-box {
    padding-bottom: 0;
  }
  
  .container-notif button {
    margin: 0;
    margin-top: 35px;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
