body {
  height: 20px;
  background-color: #FFFFFF;
}

.mainContainerLogin * {
box-sizing: border-box;
}

.mainContainerLogin {
  background-color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: 'Montserrat', sans-serif;
  height: 100vh;
  margin: -20px 0 50px;
}

.mainContainerLogin h1 {
font-weight: 600;
margin: 0;
}

.mainContainerLogin h2 {
text-align: center;
}

.mainContainerLogin p {
font-size: 14px;
font-weight: 100;
line-height: 20px;
letter-spacing: 0.5px;
margin: 20px 0 30px;
}

.mainContainerLogin span {
font-size: 12px;
}

.mainContainerLogin a {
color: #333;
font-size: 14px;
text-decoration: none;
margin: 15px 0;
}

.mainContainerLogin button {
border-radius: 15px;
border: none;
margin-top: 20px;
background-color: #112F70;
color: #FFFFFF;
font-size: 12px;
font-weight: bold;
padding: 12px 35px;
letter-spacing: 1px;
text-transform: uppercase;
transition: -webkit-transform 80ms ease-in;
transition: transform 80ms ease-in;
transition: transform 80ms ease-in, -webkit-transform 80ms ease-in;
}

.mainContainerLogin button:active {
-webkit-transform: scale(0.95);
        transform: scale(0.95);
}

.mainContainerLogin form {
background-color: #FFFFFF;
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;
padding: 0 50px;
height: 100%;
text-align: center;
}

.mainContainerLogin input {
  border: 2px solid #aaa;
  border-radius: 4px;
  box-sizing: border-box;
  padding: 8px;
  margin: 8px 0;
  outline: none;
  transition: .3s;
  width: 100%;
}

.mainContainerLogin input:focus {
  border-color: #112F70;
  box-shadow: 0 0 8px 0;
}

.idIcon input {
  padding-left: 40px;
}

.idIcon {
  position: relative;
}

.overlay-container .idIcon img {
  position: absolute;
  color: #aaa;
  left: 0;
  padding: 9px 8px;
  transition: .3s;
  top: 8px;
  width: 15%;
}

.overlay-container h2 {
  font-weight: 600;
  color: #112F70;
}

.mainContainerLogin .container {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 
      0 10px 10px rgba(0,0,0,0.22);
  position: relative;
  overflow: hidden;
  width: 768px;
  max-width: 100%;
  min-height: 480px;
}

.mainContainerLogin .form-container {
position: absolute;
top: 0;
height: 100%;
transition: all 0.6s ease-in-out;
}

.sign-in-container {
left: 0;
width: 50%;
z-index: 2;
}

.overlay-container {
position: absolute;
top: 0;
left: 50%;
width: 50%;
height: 100%;
overflow: hidden;
transition: -webkit-transform 0.6s ease-in-out;
transition: transform 0.6s ease-in-out;
transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
z-index: 100;
}

.overlay-container h1 {
  color: #112F70;
  margin-bottom: 20px;
}

.overlay-container img{
  width: 120px;
  margin-bottom: 30px;
}

.mainContainerLogin  .overlay {
background: #112F70;
color: #FFFFFF;
position: relative;
left: -100%;
height: 100%;
width: 200%;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.container.right-panel-active .overlay {
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
}

.overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
}

.overlay-left {
-webkit-transform: translateX(-20%);
        transform: translateX(-20%);
}

.overlay-right {
right: 0;
-webkit-transform: translateX(0);
        transform: translateX(0);
}

.overlay-right sup {
font-size: small;
  font-weight: normal;
}

.overlay-right img{
  width: 300px;
  margin-bottom: 30px;
}

.idIcon input {
  padding-left: 40px;
}

.idIcon {
  position: relative;
}

.overlay-container .idIcon img {
  position: absolute;
  color: #aaa;
  left: 0;
  padding: 9px 8px;
  transition: .3s;
  top: 8px;
  width: 15%;
}

.idIconReg {
  position: relative;
  width: 100%;
}

.idIconReg img {
  position: absolute;
  left: 0;
  top: 8px;
  padding: 9px 8px;
  color: #aaa;
  transition: 0.3s;
  width: 30px;
}  

.hidden {
  display: none;
}

@media only screen and (max-width: 600px) {
  .form-container {
    display: none;
  }

  .overlay-container {
    position: relative;
    align-items: center;
    width: 100%;
    left: 0;
  }

  .container {
    margin: 0 auto;
    font-size: 20px;
    height: auto;
    width: auto;
  }

  .hidden {
    display: block;
  }

  .hidden img {
    display: block;
    width: 100%;
  }

  .hidden h2 {
    font-size: 25px;
  }

  .overlay-container h1 {
    color: #112F70;
    font-size: 30px;
    margin-bottom: 20px;
  }

  form {
    width: 100%;
    height: 600px !important;
  }

  .mainContainerLogin .container {
    width: 90%;
    margin-top: 40px;
  }
}

@media only screen and (width: 320px) {
  form button {
    margin-bottom: 70px;
  }
}
